import { Button, Divider, IconButton, Stack, Typography } from '@mui/material'
import { acceptRequest, rejectRequest } from '../../../service/backend'

interface Props {
  invite: {
    id: number
    user?: {
      email: string
    }
    team?: {
      creator: {
        email: string
      }
    }
    status: string
  }
  token: string
}

export function MailItem({ invite, token }: Props) {
  const { status } = invite
  const getTextWithStatus = (status: string) => {
    switch (status) {
      case 'pending':
        return invite.user
          ? `    ${invite.user.email}`
          : `    ${invite.team.creator.email}   `
      case 'rejected':
        return `${invite.user.email}     `
      case 'accepted':
        return `${invite.user.email}    `
    }
  }

  return (
    <>
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        sx={{
          height: '5vw',
        }}
      >
        <Typography>{getTextWithStatus(status)}</Typography>

        {invite.team && (
          <Stack
            direction="row"
            justifyContent="space-between"
            sx={{
              width: 'fit-content',
            }}
          >
            <Button
              onClick={() => acceptRequest({ token, inviteId: invite.id })}
              sx={{
                bgcolor: 'success.main',
                color: 'white',
                height: '1.75vw',
                marginLeft: '1vw',
                ':hover': {
                  bgcolor: 'success.main',
                },
              }}
            >
              <img
                src="/assets/icons/tick2.svg"
                height="100%"
                style={{ marginLeft: '.5vw' }}
                alt="tick"
              />
              
            </Button>
            <Button
              onClick={() => rejectRequest({ token, inviteId: invite.id })}
              sx={{
                bgcolor: 'error.main',
                color: 'white',
                height: '1.75vw',
                marginLeft: '1vw',

                ':hover': {
                  bgcolor: 'error.main',
                },
              }}
            >
              <img
                src="/assets/icons/reject.svg"
                height="100%"
                style={{ marginLeft: '.5vw' }}
                alt="reject"
              />
              
            </Button>
            <IconButton
              sx={{
                borderWidth: '1px',
                borderColor: 'error.main',
                height: '1.75vw',
                p: 0,
              }}
            >
              <img src="/assets/icons/trash.svg" height="100%" alt="trash" />
            </IconButton>
          </Stack>
        )}
      </Stack>
      <Divider />
    </>
  )
}
